.example__container {
  background-color: var(--color-background-tertiary);
  padding: 0 20px;
}

.example__title {
  @include gap-reset;
  font: var(--font-oswald-header-xlarge);
  padding-top: 25px;
  margin-bottom: 40px;
  color: var(--color-text-primary);
}

.example__description {
  @include gap-reset;
  font: var(--font-lato-base);
  margin-bottom: 20px;
  color: var(--color-text-secondary);
}

.example__cost {
  font: var(--font-oswald-small-high);
  text-transform: uppercase;
  margin: 20px 0;
  color: var(--color-text-primary);
}

.example__result {
  font: var(--font-oswald-header);
  color: var(--color-text-primary);
}

.example__parameter {
  font: var(--font-lato-small-low);
  color: var(--color-text-secondary);
}
